import React, { Component } from "react";
import { connect } from "react-redux";
import "./Stage.less";
import { Link } from 'react-router'

@connect( ( model ) => model )

export default class Stage extends Component {

    render = () => <div id="stage">
	    <ul>
		    <li><Link to='/home'>Home</Link></li>
		    <li><Link to='/about'>About</Link></li>
	    </ul>
	    [ STAGE ]
	    {this.props.children}
    </div>

}


